import Handsontable from "handsontable";
import {renderTestCell,renderTestCheckboxCell,renderTextCell, renderTestDropdownCell, renderBrandCell, renderTdCommon, renderTypeDate, renderTypeNumber, renderTypeDropdown} from '../Renderers'

export function renderProductionDate(instance: Handsontable, td: HTMLTableCellElement, row: number, col: number, prop: string, value: string, error?: boolean): any {
    return renderTypeDate(row, td, value, error)
}

export function renderForTdCommon(instance: Handsontable, td: HTMLTableCellElement, row: number, col: number, prop: string, value: string): any {
    return renderTdCommon(instance, row,col, td, value)
}
export function renderForBrandCell(instance: Handsontable, td: HTMLTableCellElement, row: number, col: number, prop: string, value: any): any {
    return renderBrandCell(td, value)
}

export function renderBrandField(instance: Handsontable, td: HTMLTableCellElement, row: number, col: number, prop: string, value: string, error?: boolean): any {
    return renderTypeDropdown(row, td, value, error)
}

