
import { EnumFieldName } from '@/enums/EnumColumnValues'
import {validatorDate, validatorDateWithoutCallbak, validatorByCostField, validatorByBrandField, validatorByStatusField} from '../validators/ValidatorFunctions'
import { renderProductionDate,renderCostField, renderBrandField, renderForTdCommon , renderForBrandCell } from './HSTableHelper/FieldsRenderFunctions'
import { instance } from './InstanceHelper'
import { storage } from '../store/index'
import {setDataAtRowProp, setDataAtRowPropTest} from '../helpers/HSFormula/analizatorFormula/CalculateMethods'
import { fieldsInfoMap, IField } from './ColumnsHelper'


export function afterChangeHook(changesParam: any[], source: any): any {
    //ХУК ДЛЯ работа с формулами после редактирования
    if (!changesParam.length) return false
    const fileldName: EnumFieldName = changesParam[0][1] as EnumFieldName
    const changes: [number, any][] = changesParam.map((change: any[]) => [change[0], change[3]])
    setDataAtRowProp(changes, fileldName)
}

export function beforeChangeHook(changes: any[], selectColumn: number): any {
    //console.log('beforeChangeHook', changes)
    //ХУК ДЛЯ ВАЛИДАЦИИ И ОТОБРАЖЕНИИ КРАСАНОЙ ЯЧЕЙКИ ЕСЛИ НЕ ПРОШЛА ВАЛИДАЦИЯ
    if (!changes.length) return false

    const getTdCell = (row: number): HTMLTableCellElement => instance.getCell(row, selectColumn) as HTMLTableCellElement
    
    const changedRow: number = changes[0][0] as number
    const fileldName: string = changes[0][1] as string
    const newValue: string = changes[0][3] as string
    
    switch(fileldName) {
        case EnumFieldName.ProductionDate:
            if (validatorDateWithoutCallbak(newValue) === false) {
                //если не прошло валидацию то окрасить ячейку красным
                renderProductionDate(instance, getTdCell(changedRow), changedRow, selectColumn, fileldName, newValue, true )
                return false
            }
            break
        case EnumFieldName.CostOne:
            if (validatorByCostField(newValue) === false) {
                //если не прошло валидацию то окрасить ячейку красным
                renderCostField(instance, getTdCell(changedRow), changedRow, selectColumn, fileldName, newValue, true )
                return false
            }
            break
        case EnumFieldName.CostTwo:
            if (validatorByCostField(newValue) === false) {
                //если не прошло валидацию то окрасить ячейку красным
                renderCostField(instance, getTdCell(changedRow), changedRow, selectColumn, fileldName, newValue, true )
                return false
            }
            break
        case EnumFieldName.Brand:
            if (validatorByBrandField(newValue) === false) {
                //если не прошло валидацию то окрасить ячейку красным
                renderBrandField(instance, getTdCell(changedRow), changedRow, selectColumn, fileldName, newValue, true )
                return false
            }
            break
        case EnumFieldName.Status:
            if (validatorByStatusField(newValue) === false) {
                //если не прошло валидацию то окрасить ячейку красным
                renderBrandField(instance, getTdCell(changedRow), changedRow, selectColumn, fileldName, newValue, true )
                return false
            }
            break
    }

}
