<style lang="scss">
.settings-menu-drop-down {
    width:350px;
    user-select: none;
    
&__body {
   
    
}

&__row {
    padding: 6px 10px 6px 10px;
    white-space: normal;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;

    &:hover {
        border: none;
        background-color: #f1f3f4;
    }
}

&__img {
    margin-right: 10px !important;
    width: 18px;
    height: 18px;
}

&__text {
    color: #202124;
    font-size: 14px;
    letter-spacing: .2px;
    line-height: 20px;
    font-family: Roboto400;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
&__name {
    
}
&__code {
    color: rgb(197, 197, 197);
    font-size: 14px;
    text-align: end;
    letter-spacing: .2px;
    line-height: 20px;
    font-family: GoogleSans-Bold;
    color: #898f94;
}
}

</style>

<template>
    <div class="settings-menu-drop-down">
        <div class="settings-menu-drop-down__body">
            <div class="settings-menu-drop-down__row" @click="showSettingsColumnsTable">
                <div class="settings-menu-drop-down__text">
                    <span class="settings-menu-drop-down__name">Управление столбцами</span>
                    <span class="settings-menu-drop-down__code">Shift+Ctrl+Q</span>
                </div>
            </div>
            <div class="settings-menu-drop-down__row" @click="showSettingsHeaderTable">
                <div class="settings-menu-drop-down__text">
                    <span class="settings-menu-drop-down__name">Настройка шапки</span>
                    <span class="settings-menu-drop-down__code">Shift+Ctrl+F</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts" setup>
import { visibleDialogSettingsHeader, visibleDropDownMenu, visibleDropDownSettingsMenu, visibleDialogSettingsColumns } from '@/helpers/States/StateDialogs';
function showSettingsHeaderTable() {

    //отключение выпадашек
    visibleDropDownMenu.value = false
    visibleDropDownSettingsMenu.value = false
    
    //отключение других окон
    visibleDialogSettingsColumns.value = false

    //включение выбранного окна
    visibleDialogSettingsHeader.value = true


}
function showSettingsColumnsTable() {
    visibleDropDownMenu.value = false
    visibleDropDownSettingsMenu.value = false

    //отключение других окон
    visibleDialogSettingsHeader.value = false
    //включение выбранного окна
    visibleDialogSettingsColumns.value = true
}


</script>