import { MutationTree } from 'vuex';
import {  ConfigData } from './types';
import { IPayloadAddRuleToState, IPayloadDeleteRuleToState } from '@/types/FormattingTypes';
import { instance } from '@/helpers/InstanceHelper';

export const mutations: MutationTree<ConfigData> = {

  ADD_RULE(state: ConfigData,payloadRule: IPayloadAddRuleToState) {
    const rules = state.rulesMap.get(payloadRule.columnNumber)
    if (rules) {
      rules.push(payloadRule.rule)
    } else {
      state.rulesMap.set(payloadRule.columnNumber, [payloadRule.rule])
    }
    /* instance.render() */
  },
  EDIT_RULE(state: ConfigData,payloadRule: IPayloadAddRuleToState) {
    const rules = state.rulesMap.get(payloadRule.columnNumber)
    if (rules) {
      const editableRuleIndex = rules.findIndex(rule => rule.id === payloadRule.rule.id)
      
      rules[editableRuleIndex] = payloadRule.rule
    }
    /* instance.render() */
  },
  DELETE_RULE(state: ConfigData, payloadRule: IPayloadDeleteRuleToState) {
    const rules = state.rulesMap.get(payloadRule.columnNumber)
    
    if (rules && rules.length) {

      const indexRule: number = rules.findIndex(rule => rule.id === payloadRule.idRule)
      
      //если есть такое правило то удалить
      if (indexRule !== -1) {
        rules.splice(indexRule, 1)
      }
      //если в массиве не осталось правил, то удалить весь мап по колонке
      if (!rules.length)  {
        state.rulesMap.delete(payloadRule.columnNumber)
      }
        
      /* instance.render() */
    }
  },
  DELETE_ALL_RULES(state: ConfigData, columnNumber: number) {
    const rules = state.rulesMap.get(columnNumber)
    if (rules) {
      state.rulesMap.delete(columnNumber)
      /* instance.render() */
    }
  }
};