<style lang="scss">

.formatting-rules-list {
    &__body {
        overflow: hidden;
        height: 100%;
    }

    &__items {
        display: flex;
        flex-direction: column;
        position: relative;
    }

    &__item {

    }
}

.item-formatting-rule {
    border-top: 1px solid #cccccc;
    cursor: pointer;
    height: 75px;
    padding: 10px;
    
    position: relative;

    &:hover {
        background-color: #f3f3f3;
    }

    &__body {
        height: 56px;
        width: 100%;
        position: relative;
        background-color: transparent;
        display: grid;
        grid-template-columns: 50px auto;
        gap: 10px;
    }
    &__icon-btn {
        position: absolute;
        top: 15px;
        right: 30px;
    }
    &__icon-vert {
        position: absolute;
        top: 15px;
        left: -17px;
    }
    &__preview-cell {
        border: 1px solid rgb(217, 217, 217);
        display: flex;
        display: flex;
        align-items: center;
        padding: 1px;
        

        div {
            width: 100%;
        }
    }
    &__description {
        margin-bottom: auto;
        margin-right: 8px;
        margin-top: auto;
        position: relative;
        width: 142px;
    }

    &__description-rule-text {
        color: #333;
        font-size: 13px;
        line-height: 15px;
        max-height: 30px;
        margin-bottom: 4px;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 142px;
        font-family: Roboto500;
    }

    &__description-type-rule-text {
        color: #555;
        height: 14px;
        font-size: 12px;
        line-height: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 142px;
    }
}

.ghost {
    background-color: #f3f3f3;
}
</style>

<template>
    <div class="formatting-rules-list">
        <div class="formatting-rules-list__body">
            <draggable
                :list="currentRules"
                :animation="100"
                item-key="id"
                class="formatting-rules-list__items"
                :forceFallback="false"
                ghost-class="ghost"
                @end="renderTable"
                >
                <template #item="{ element }" >
                        <div @click="emit('editRule', element)" class="item-formatting-rule" @mouseover="mouseoverRule(element.id)" @mouseout="mouseoutRule(element.id)">
                            <div class="item-formatting-rule__body">
                                <div :style="element.style" class="item-formatting-rule__preview-cell">
                                    <div :style="element.style">123</div>
                                </div>
                                <div class="item-formatting-rule__description">
                                    <div class="item-formatting-rule__description-rule-text">{{ element.textRule }}</div>
                                    <div class="item-formatting-rule__description-type-rule-text">{{ element.isMacros ? 'Это макрос' : 'Это правило' }}</div>
                                </div>
                                <div @click.stop="deleteRule(element)" class="item-formatting-rule__icon-btn" v-show="mouseovered && element.id === currentId">
                                    <q-icon size="24px" name="delete_forever" style="color: #b9bcbf;"></q-icon>
                                    <q-tooltip class="custom-tooltip-menu" anchor="bottom middle" self="top middle" :offset="[10, 5]">
                                        <span class="cursor-carret_none">Удалить {{ element.isMacros ? 'макрос' : 'правило' }}</span><br/>
                                    </q-tooltip>
                                </div>
                                <!-- <div class="item-formatting-rule__icon-vert" v-show="mouseovered">
                                    <q-icon size="24px" name="more_vert" style="color: #b9bcbf;"></q-icon>
                                </div> -->
                            </div>
                            
                        </div>
                </template>
            </draggable>
        </div>
    </div>
</template>

<script lang="ts" setup>
import Vue3DraggableResizable from 'vue3-draggable-resizable'
import { defineProps, ref, defineEmits, computed } from 'vue';
import draggable from 'vuedraggable'
import { IConditionFormattingRules, IPayloadAddRuleToState, IPayloadDeleteRuleToState } from '@/types/FormattingTypes';
import { watch } from 'vue';
import { useStore } from 'vuex';
import { instance } from '@/helpers/InstanceHelper';
const store = useStore()

const emit = defineEmits(['editRule'])

let currentRules = ref<IConditionFormattingRules[]>([])
const rules = computed(() => store.getters.getRulesByColumn(store.getters.selectedColumnCommonUse))

watch(rules, (newValue: IConditionFormattingRules[]) => {
    if (newValue !== undefined) {
        currentRules.value = newValue
    } else {
        currentRules.value = []
    }
}, {deep: true, immediate: true})

let mouseovered = ref(false)
let currentId = ref<string | null>(null)

function deleteRule(element: any) {
    const selected: any[] = instance.getSelected()
    const endCol = selected[0][3] as number

    setTimeout(() => {
        store.dispatch('deleteRule', {columnNumber: endCol, idRule: element.id} as IPayloadDeleteRuleToState)
        renderTable()  
    })
}

function mouseoverRule(id: string) {
    currentId.value = id
    mouseovered.value = true
}

function mouseoutRule(id: string) {
    currentId.value = null
    mouseovered.value = false
}

function renderTable() {
    setTimeout(()=> {
        instance.render()
    })
}
</script>