<template>
    <contenteditable 
        @keyup="keydownInput($event)"
        @mousedown="mousedownInput($event)"
        class="formula-block__input" 
        id="custom-input"
        tag="div"
        :contenteditable="true" 
        v-model="contentedTableValue" 
        :no-nl="true" 
        :no-html="true" 
        @returned="enterPressed" 
    />
</template>

<script setup lang="ts">
import contenteditable from 'vue-contenteditable'
import { ref, defineEmits, computed, watch, reactive , onMounted, getCurrentInstance } from 'vue';
import {getCaretPosition, getCurrentPositionByEventMosedown} from '../../helpers/HSFormula/CommonMethods'
import insertTextAtCursor from 'insert-text-at-cursor';
import {lex, mathOperatorLex} from '../../helpers/HSFormula/analizatorFormula/lexer'
import {formulaMap} from '../../helpers/HSFormula/FormulaHelper'
import FormulaParser from 'fast-formula-parser';
import { useStore } from 'vuex'
import {IFormula, IResultFormula, IUserFormula} from '../../helpers/HSFormula/Types'
import {getTestResultFormulaAtInputedText} from '../../helpers/HSFormula/analizatorFormula/CalculateMethods'
import { EnumTypeField } from '@/enums/EnumsByFilter';

const emit = defineEmits(['changeResultFormula'])
const parser: FormulaParser = new FormulaParser({}) as FormulaParser

const store = useStore()
let currentCaretNumber = ref(0)
let leftColumnName = ref('')
let leftColumnNumber = ref('')
let rightColumnName = ref('')
let rightColumnNumber = ref('')
let rightColumn = ref(false)
let contentedTableValue = ref('')
let customInputDataTest = ref('')
let arrayDataForHtml = ref<any[]>([])

let highlightedMode = ref(false)
let isActiveReplace  = ref(false)

const setSelectedColumnHeaderClickLeftBtn = computed(() => store.getters.setSelectedColumnHeaderClickLeftBtn)

watch(setSelectedColumnHeaderClickLeftBtn, (newValue: number) => {
    let formulaObject: IUserFormula = store.getters.formulaObjectByColumn(newValue)
    if (formulaObject) {
        contentedTableValue.value = "="+formulaObject.formulaTextOriginal
    } else {
        contentedTableValue.value = ""
    } 
})

const formulaFunctions =[...formulaMap.keys()] as string[]
const highlightedRangeCells = computed((value: any) => {
    return store.getters.highlightedRangeCells
})
const dataByHighlightedRangeCells = computed(() => {
    return store.getters.dataByHighlightedRangeCells
})

const mathOperators = [';','(','=','{','*','+','-','/','&','^','%','>','<','','']

watch(highlightedMode, (newValue: boolean) => {
    if (newValue !== true) {
        isActiveReplace.value = false 
    }
})

watch(currentCaretNumber, (newValue: number) => {
    //console.log('currentCaretNumber', newValue)
    let preSimbolAtCarretPosition = contentedTableValue.value[newValue - 1]
    //если  символ перед кареткой содержит оператор, то можно вставлять диапазон
    highlightedMode.value = mathOperators.includes(preSimbolAtCarretPosition) ? true : false
})


watch(contentedTableValue, (valueText: string, oldValueText: string) => {
    //const customInput: HTMLDivElement = document.getElementById('custom-input') as HTMLDivElement
    
    if (valueText.length > 1 && valueText[0] === '=') {
        arrayDataForHtml.value = []
        
        let valueTextLocal = valueText.slice(1)

        //заменить имена колонок диапазонов на тестовые данные
        const resultFormula = getTestResultFormulaAtInputedText(valueTextLocal)
        /* console.log('resultFormula', resultFormula) */
       // console.log('111resultFormula', resultFormula)
       /* const lexResult = lex(valueTextLocal);
       console.log('lexResult', lexResult)  */
        emit('changeResultFormula', resultFormula)
    }
})

function keydownInput(event: any) {
    const customInput: HTMLDivElement = document.getElementById('custom-input') as HTMLDivElement
    currentCaretNumber.value = getCaretPosition(customInput)
}

function mousedownInput(event: any) {
    //нахождение каретки по мыши
    getCurrentPositionByEventMosedown().then((result: any) => currentCaretNumber.value = result)
}
function setCaretPosition(caretPos: number) {
   
    const customInput: any = document.getElementById('custom-input') as HTMLDivElement
    customInput.setSelectionRange(caretPos, caretPos);
    
           /*  range.move('character', caretPos);
            range.select(); */
    /* if(customInput != null) {
        if(customInput.createTextRange) {
            var range = customInput.createTextRange();
            range.move('character', caretPos);
            range.select();
            console.log('111')
        }
        else {
            console.log('222')
            if(customInput.selectionStart) {
                customInput.focus();
                customInput.setSelectionRange(caretPos, caretPos);
            }
            else
            console.log('333')
            customInput.focus();
        }
    } */
}



watch(highlightedRangeCells, (newValue: any, oldValue: string) => {

const customInput: HTMLDivElement = document.getElementById('custom-input') as HTMLDivElement

const r = /\d+/; 
if (newValue && newValue.indexOf(":") === -1) {
    //если только левая
    rightColumn.value = false
    leftColumnNumber.value = newValue.match(r)[0]
    leftColumnName.value = newValue.replace(/[0-9]/g, '')

    rightColumnNumber.value = ''
    rightColumnName.value = ''
} else if (newValue && newValue.indexOf(":") !== -1) {
    rightColumn.value = true
    leftColumnName.value = newValue.split(':')[0].replace(/[0-9]/g, '')
    leftColumnNumber.value = newValue.split(':')[0].match(r) !==null ? newValue.split(':')[0].match(r)[0] : ''
    rightColumnNumber.value = newValue.split(':')[1].match(r) !==null ? newValue.split(':')[1].match(r)[0] : ''
    rightColumnName.value =  newValue.split(':')[1].replace(/[0-9]/g, '')
}
//если вставка в формулу
if (highlightedMode.value === true) {

    let newRange = ''
    if (rightColumnName.value.length) {
        newRange = `${leftColumnName.value}:${rightColumnName.value}`.toLocaleUpperCase()
    } else {
        newRange = leftColumnName.value.toLocaleUpperCase()
    }

    let tokens: string[] =  contentedTableValue.value.split('')

    if (isActiveReplace.value === false) {
        tokens.splice(currentCaretNumber.value, 0, newRange)
        //включить возможность замены
        isActiveReplace.value = true

    } else {
        let oldValueLocal = oldValue.replace(/[0-9]/g, '')
        tokens.splice(currentCaretNumber.value, oldValueLocal.length, newRange)
    }

    //вставка 
    contentedTableValue.value = tokens.join('')
    //setCaretPosition(6)
    //customInput.focus()
    /* setTimeout(() => {
        
        setCaretPosition(5)
    }); */
    //setCaretPosition(5)
    }
}
)



function enterPressed(event: any) {
    console.log('enterPressed', event)
}
</script>