<style lang="scss">

    /* font-family: GoogleSans-Regular; */


.settings-header {
    /* width: 380px; */
    display: flex;
    flex-direction: column;
    gap: 20px;
    
    &__block-checkboxs {
        font-size: 14px;
        color: #3c4043;
        font-family: Roboto400;
        margin-left: -10px;
    }

    &__block-colors {
        display: flex;
        flex-direction: column;
        gap: 5px;
        font-family: Roboto400;
    }

    &__block-colors-title {
        color: #3c4043;
        font-family: Roboto700;
        font-size: 14px;
        letter-spacing: .25px;
        
    }

    &__item-block-color {
        display: grid;
        grid-template-columns: 1fr auto;
        align-items: center;
    }

    &__block-color-text {
        font-size: 14px;
        color: #3c4043;
        font-family: Roboto400;
    }

    &__block-color-input {
        width: 160px;
        height: 40px !important;
        /* color: #3c4043 !important; */
        font-family: GoogleSans-Regular;

    }
}

</style>
<template>
        <div class="settings-header">
            <div class="settings-header__block-checkboxs">
                <q-checkbox v-model="showGroupRow"  label="Показывать строку групп (верхняя строка)" />
                <q-checkbox v-model="showSearchRow" label="Показывать строку поиска (средняя строка)" />
            </div>
            <!-- <div class="hr-custom"></div> -->
            <div class="settings-header__block-colors">
                <span class="settings-header__block-colors-title">Цвет фона</span>
                <div class="settings-header__item-block-color" >
                    <span class="settings-header__block-color-text"><span>{{ EnumGroupColumnName.GroupColumn1 }}</span></span>
                    <q-input
                        outlined
                        dense
                        v-model="colorGroupRowColumn1"
                        class="settings-header__block-color-input"
                        :style="{'background-color': colorGroupRowColumn1}"
                    >
                        <template v-slot:append>
                        <q-icon size="xs" color="grey-8" name="format_color_fill" class="cursor-pointer">
                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                <q-color no-header-tabs v-model="colorGroupRowColumn1" />
                            </q-popup-proxy>
                        </q-icon>
                        </template>
                    </q-input>
                </div>
                <div class="settings-header__item-block-color" >
                    <span class="settings-header__block-color-text"><span>{{ EnumGroupColumnName.GroupColumn2 }}</span></span>
                    <q-input
                        outlined
                        dense
                        v-model="colorGroupRowColumn2"
                        class="settings-header__block-color-input"
                        :style="{'background-color': colorGroupRowColumn2}"
                    >
                        <template v-slot:append>
                        <q-icon size="xs" color="grey-8" name="format_color_fill" class="cursor-pointer">
                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                <q-color no-header-tabs v-model="colorGroupRowColumn2" />
                            </q-popup-proxy>
                        </q-icon>
                        </template>
                    </q-input>
                </div>
                <div class="settings-header__item-block-color" >
                    <span class="settings-header__block-color-text"><span>{{ EnumGroupColumnName.GroupColumn3 }}</span></span>
                    <q-input
                        outlined
                        dense
                        v-model="colorGroupRowColumn3"
                        class="settings-header__block-color-input"
                        :style="{'background-color': colorGroupRowColumn3}"
                    >
                        <template v-slot:append>
                        <q-icon size="xs" color="grey-8" name="format_color_fill" class="cursor-pointer">
                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                <q-color no-header-tabs v-model="colorGroupRowColumn3" />
                            </q-popup-proxy>
                        </q-icon>
                        </template>
                    </q-input>
                </div>
            </div>
            <!-- <div class="hr-custom"></div> -->
            <div class="settings-header__actions">
                <button class="btn-custom" @click.stop="saveSettings">Сохранить</button>
            </div>
        </div>  

        
</template>

<script setup lang="ts">
import { ref, defineEmits, reactive, toRaw } from 'vue';
import { LocalStorageHandler } from '../../utils/LocalStorageHandler';
import {nestedHighHeaderGroup, nestedSearchRowHeader, nestedHighHeader} from '../../helpers/ColumnsHelper'
import {instance} from '../../helpers/InstanceHelper'
import {EnumGroupColumnName} from '../../enums/EnumColumnValues'
import {IColumnConfifuration} from '../../types/LocalStorageTypes'

const emit = defineEmits(['closeDialog'])

const showGroupRow = ref(LocalStorageHandler.getItem('showGroupRow') ?? false)
const showSearchRow = ref(LocalStorageHandler.getItem('showSearchRow') ?? false)

const colorGroupRowColumn1 = ref<string>(LocalStorageHandler.getItem('colorGroupRowColumn1') ?? '#ffffff')
const colorGroupRowColumn2 = ref<string>(LocalStorageHandler.getItem('colorGroupRowColumn2') ?? '#ffffff')
const colorGroupRowColumn3 = ref<string>(LocalStorageHandler.getItem('colorGroupRowColumn3') ?? '#ffffff')

function saveSettings() {
    LocalStorageHandler.setItem('showGroupRow', showGroupRow.value);
    LocalStorageHandler.setItem('showSearchRow', showSearchRow.value);

    LocalStorageHandler.setItem('colorGroupRowColumn1', colorGroupRowColumn1.value);
    LocalStorageHandler.setItem('colorGroupRowColumn2', colorGroupRowColumn2.value);
    LocalStorageHandler.setItem('colorGroupRowColumn3', colorGroupRowColumn3.value);

    const showGroupRowLocal = LocalStorageHandler.getItem('showGroupRow') as boolean
    const showSearchRowLocal = LocalStorageHandler.getItem('showSearchRow') as boolean

    let headerColumnsConfifuration: IColumnConfifuration = {
        activeGroupRow: showGroupRowLocal,
        activeSearchRow: showSearchRowLocal,
        activeFilterRow: true
    }

    LocalStorageHandler.setItem('headerColumnsConfifuration', headerColumnsConfifuration);

    if (showGroupRowLocal === true && showSearchRowLocal === true) {
        instance.updateSettings({
            nestedHeaders: [nestedHighHeaderGroup,nestedSearchRowHeader, nestedHighHeader]
        })
    } else if (showGroupRowLocal === false && showSearchRowLocal === true) {
        instance.updateSettings({
            nestedHeaders: [nestedSearchRowHeader, nestedHighHeader]
        })
    } else if (showGroupRowLocal === true && showSearchRowLocal === false) {
        instance.updateSettings({
            nestedHeaders: [nestedHighHeaderGroup, nestedHighHeader]
        })
    } else {
        instance.updateSettings({
            nestedHeaders: [nestedHighHeader]
        })
    }
}

</script>