<style lang="scss">
.formatting {
    overflow: hidden;

    &__body {
        display: flex;
        flex-direction: column;
    }

    &__toggle-content {
        /* border: 1px solid red; */
    }

    &__rules-list {
        height: 100%;
    }

    &__form-create-rule {
    }

    &__actions {
        border: 1px solid transparent;
        display: flex;
        flex-direction: column;
        gap: 5px;
        align-items: flex-start;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 0px 15px;
        height: 60px;
        /* border-bottom: 1px solid #dadce0; */
    }

    &__actions-btn {
        padding: 6px 6px 6px 20px;
        border-radius: 4px;

        &:hover {
            background-color: #1976d215;
        }
    }

    &__add-macros-btn {
        color: #42bdae !important;
        padding: 6px 6px 6px 22px;
        border-radius: 4px;

        &:hover {
            background-color: #42bdaf11;
        }
    }

    &__actions-icon {
        position: relative;
 
        &::before {
            content: '\002B';
            font-size: 24px;
            position: absolute;
            left: -18px;
            top: -1px;
            font-size: 24px;
        }
    }
}
</style>

<template>
    <div class="formatting">
        <div class="formatting__body">
            <div class="formatting__actions">
                <button class="btn-out-custom formatting__actions-btn" @click="createRule">
                    <span style="content: \002B;"></span>
                    <span class="formatting__actions-icon">Добавить правило</span>
                </button>
                <button disabled class="btn-out-custom formatting__add-macros-btn">
                    <span style="content: \002B;"></span>
                    <span class="formatting__actions-icon">Добавить макрос</span>
                </button>
            </div>
            <div class="formatting__toggle-content">
                <div class="formatting__rules-list" v-if="showRulesList">
                    <ThisConditionalFormattingRulesList @editRule="editRule"></ThisConditionalFormattingRulesList>
                </div>
                <div class="formatting__form-create-rule" v-else>
                    <ThisConditionalFormattingRuleCreateForm ref="child" @closeFormCreateRule="showRulesList=true"></ThisConditionalFormattingRuleCreateForm>
                </div>
            </div>

        </div> 
    </div>
</template>

<script setup lang="ts">
import { ref } from 'vue';
import ThisConditionalFormattingRulesList from './ThisConditionalFormattingRulesList.vue'
import ThisConditionalFormattingRuleCreateForm from './ThisConditionalFormattingRuleCreateForm.vue'
import { IConditionFormattingRules } from '@/types/FormattingTypes';
import { nextTick } from 'vue';

const child = ref();
let showRulesList = ref(true)

/* let editingRule = ref<IConditionFormattingRules>({
    isMacros: false,
    id: '',
    style: '',
    jsCode: '',
    fieldName: EnumColumnName.Type,
    appliedRule: EnumRuleFormatting.includedata,
    appliedRuleObject: undefined,
    textRule: '',
    fieldType: EnumTypeField.String,
    typeInputs: EnumTypeFormattingRuleInputs.Default,
    styleConfig: {
    formattingBoldActive: false,
    formattingItalicActive: false,
    formattingUnderlinedActive: false,
    formattingStrikethroughActive: false,
    formattingTextColorActive: false,
    formattingFillColorActive: false,
    formattingAlignPosition: {
    left: false,
    center: false,
    right: false
    }
    },
    value1: '',
    value2: 0
}) */

function createRule() {
    
    let showRulesListLocal = showRulesList.value
    showRulesList.value = false
    
    nextTick(() =>{
        if (!showRulesListLocal) {
            child.value.createNewRule()
        } else {
            child.value.clearFormattingSettings()
        }
    })
}

function editRule(rule: IConditionFormattingRules) {

    if (rule) {
        showRulesList.value = false

        nextTick(() =>{
            child.value.editRule(rule)
        })
    }
}

</script>