
import { ActionTree } from 'vuex';
import {  ConfigData } from './types';
import { RootState } from '../types';
import { IPayloadAddRuleToState, IPayloadDeleteRuleToState } from '@/types/FormattingTypes';

export const actions: ActionTree<ConfigData, RootState> = {
    
  addRule({commit}, payloadRule: IPayloadAddRuleToState) {
    commit('ADD_RULE', payloadRule)
  },
  editRule({commit}, payloadRule: IPayloadAddRuleToState) {
    commit('EDIT_RULE', payloadRule)
  },
  deleteRule({commit}, payloadRule: IPayloadDeleteRuleToState) {
    commit('DELETE_RULE', payloadRule)
  },
  deleteAllRules({commit}, columnNumber: number) {
    commit('DELETE_ALL_RULES', columnNumber)
  }
};