<template>
  <!-- oncontextmenu="return false" -->
  <div class="app"  @keydown="keydownGlobal">
    <div class="app__managerboard" v-if="visibleUpperPanel">
      <the-managerboard @click.stop="mousedownOnManagerboard"></the-managerboard>
    </div>
    <div :style="tableHeightStyle" class="app__workspace">
      <the-workspace></the-workspace>
    </div>
	</div>
</template>

<script lang="ts" setup>
import { ref, onMounted, computed } from 'vue';
import TheManagerboard from './components/TheManagerboard.vue'
import TheWorkspace from './components/TheWorkspace.vue'
import {IStoryRow} from './components/TheFormShowStoryRow.vue'
import { LocalStorageHandler } from './utils/LocalStorageHandler';
import {IColumnConfifuration} from './types/LocalStorageTypes'
import { useStore } from 'vuex'
import hotkeys from 'hotkeys-js';
import {visibleDialogSettingsHeader, visibleDialogSettingsColumns, visibleDropDownMenu, visibleDropDownSettingsMenu, visibleCustomDraggableDialog, visibleContextMenu, closeContextMenu} from './helpers/States/StateDialogs'
const store = useStore()

const visibleUpperPanel = computed(() => store.getters.visibleUpperPanel)
onMounted(() => {
  if (LocalStorageHandler.getItem('showSearchRow') === null) {
    let headerColumnsConfifuration: IColumnConfifuration = {
      activeGroupRow: true,
      activeSearchRow: true,
      activeFilterRow: true
    }

    LocalStorageHandler.setItem('headerColumnsConfifuration', headerColumnsConfifuration);
  }

  //изначально фикс колонка 0
  if (LocalStorageHandler.getItem('posFixedColumn') === null) {
    LocalStorageHandler.setItem('posFixedColumn', 0);
  }   
  
  if (LocalStorageHandler.getItem('visibleUpperPanel') === null) {
    LocalStorageHandler.setItem('visibleUpperPanel', true);
  }   
})

function hideDropDownMenu() {
  if (visibleDropDownSettingsMenu.value) visibleDropDownSettingsMenu.value = false
  visibleDropDownMenu.value = false
}

function mousedownOnManagerboard() {
  closeContextMenu()
  hideDropDownMenu()
}

function keydownGlobal(event: any) {
  hotkeys('shift+ctrl+q, shift+ctrl+f', function (event, handler){
  switch (handler.key) {
    case 'shift+ctrl+q': 
      visibleDialogSettingsHeader.value = true
      if (visibleDialogSettingsColumns) visibleDialogSettingsColumns.value = false
      break; 
    case 'shift+ctrl+f': 
      visibleDialogSettingsColumns.value = true

      if (visibleDialogSettingsHeader) visibleDialogSettingsHeader.value = false
      break;
      default:
      visibleCustomDraggableDialog.value = true
  }
});
}

const tableHeightStyle = computed(() => {
  return visibleUpperPanel.value === true ? 'min-height: calc(100vh - 45px);' : 'min-height: calc(100vh);'
})
</script>

<style lang="css">
.material {
  cursor: pointer;
}

.app__popup {
  position: absolute;
	inset: 0;
	background-color: #21212108;
	z-index: 1000;
}
.app__popup-after {
  position: absolute;
	inset: 0;
	background-color: #21212108;
	z-index: 1001;
}
.app {
  background-color: #f9fbfd !important;
  /* background-color: #f1efe7; */
  width: 100%;
  flex-direction: column;
  
}
.app__managerboard {
  height: 45px;
}
.app__workspace {
  background-color: #f9fbfd !important;
  /* background-color: #ffcc0007; */

}

		
</style>
